@mixin vfi-text-color {
  outline: $vfi-outline-width $vfi-outline-style currentColor;
}

@mixin vfi-text-color-inset {
  // IE 11 does not support inset outlines, so outline color will need to be set to "initial" to use "invert" value in IE11,
  // while browsers for which "invert" is invalid will use "currentColor" as their "initial" value instead.
  outline: $vfi-outline-width $vfi-outline-style; // In shorthand, color gets set to "initial" value when not specified
  outline-offset: $vfi-outline-inset;
}
