@import "../../styles/functions";
@import "../../variables";
@import "../../styles/mixins";

// .modal-open      - body class for killing the scroll
// .modal           - container to scroll within
// .modal-dialog    - positioning shell for the actual modal
// .modal-content   - actual modal w/ bg and corners and stuff


.modal-open {
  // Kill the scroll on the body
  overflow: hidden;

  .modal {
    overflow-x: hidden;
    overflow-y: auto;
  }
}

// Container that the modal scrolls within
.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: $zindex-modal;
  display: none;
  width: 100%;
  height: 100%;
  overflow: hidden;

  // Prevent Chrome on Windows from adding a focus outline. For details, see
  // https://github.com/twbs/bootstrap/pull/10951.
  outline: 0;
  // We deliberately don't use `-webkit-overflow-scrolling: touch;` due to a
  // gnarly iOS Safari bug: https://bugs.webkit.org/show_bug.cgi?id=158342
  // See also https://github.com/twbs/bootstrap/issues/17695
}

// Shell div to position the modal with bottom padding
.modal-dialog {
  position: relative;
  width: auto;
  margin: 0 auto;

  // Match spacing to container
  padding: 0 ($grid-gutter-width / 2);
  box-sizing: content-box;

  // allow clicks to pass through for custom click handling to close modal
  pointer-events: none;

  // When fading in the modal, animate it to slide down
  .modal.fade & {
    @include transition($modal-transition);
    transform: $modal-fade-transform;
  }
  .modal.show & {
    transform: $modal-show-transform;
  }

  // Vertically center modal content
  display: flex;
  align-items: center;
  min-height: 100%;

  // Ensure modals extend the full height of the view (IE10/11)
  &::before {
    display: block; // IE10
    height: 100vh;
    content: "";
  }
}

.modal-dialog-scrollable {
  display: flex; // IE10/11
  max-height: 100%;

  // Ensure `.modal-body` shows scrollbar (IE10/11)
  flex-direction: column;
  justify-content: center;
  height: 100%;

  &::before {
    content: none;
  }

  .modal-content {
    overflow: hidden;
    max-height: none; // IE10/11
  }

  .modal-header,
  .modal-footer {
    flex-shrink: 0;
  }

  .modal-body {
    overflow-y: auto;
  }
}

// Actual modal
.modal-content {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%; // Ensure `.modal-content` extends the full width of the parent `.modal-dialog`
  // counteract the pointer-events: none; in the .modal-dialog
  pointer-events: auto;
  background-color: $modal-content-bg;
  background-clip: padding-box;
  border: $modal-content-border-width solid $modal-content-border-color;
  @include border-radius($modal-content-border-radius);
  @include box-shadow($modal-content-box-shadow-xs);
  // Remove focus outline from opened modal
  outline: 0;

  @include theme-inverse {
    background-color: $modal-content-bg-inverse;
  }
}

// Modal background
.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: $zindex-modal-backdrop;
  width: 100vw;
  height: 100vh;
  background-color: $modal-backdrop-bg;

  // Fade for backdrop
  &.fade { opacity: 0; }
  &.show { opacity: $modal-backdrop-opacity; }
}

// Modal header
// Top section of the modal w/ title and dismiss
.modal-header {
  display: flex;
  align-items: flex-start; // so the close btn always stays on the upper right corner
  justify-content: space-between; // Put modal header elements (title and dismiss) on opposite ends
  padding: $modal-header-padding;
  @include border-top-radius($modal-content-border-radius);

  .close {
    padding: $modal-close-padding;

    // Position close button at end of header
    @include ltr {
      margin-left: auto;
    }
    @include rtl {
      margin-right: auto;
    }

    &::before {
      display: block;
    }
  }
}

// Title text within header
.modal-title {
  margin-bottom: 0;
  line-height: $modal-title-line-height;

  // Offset to prevent overlap with close button
  @include ltr {
    margin-right: $modal-title-margin-end;
  }
  @include rtl {
    margin-left: $modal-title-margin-end;
  }
}

// Modal body
// Where all modal content resides (sibling of .modal-header and .modal-footer)
.modal-body {
  position: relative;
  // Enable `flex-grow: 1` so that the body take up as much space as possible
  // when should there be a fixed height on `.modal-dialog`.
  flex: 1 1 auto;
  padding: $modal-inner-padding-top $modal-inner-padding-x $modal-inner-padding-bottom;

  &:not(:last-child) {
    padding-bottom: 0;
  }

  // Prevent last element from impacting space beneath end of content
  > *:last-child {
    margin-bottom: 0;
  }
}

// Footer (for actions)
.modal-footer {
  display: flex;
  align-items: center; // vertically center
  justify-content: flex-end; // Right align buttons with flex property because text-align doesn't work on flex items
  padding: $modal-footer-padding-y $modal-footer-padding-x;
  @include border-bottom-radius($modal-content-border-radius);

  // Easily place margin between footer elements
  > :not(:first-child) { margin-left: 0.25rem; }
  > :not(:last-child) { margin-right: 0.25rem; }
}

// Measure scrollbar width for padding body during modal show/hide
.modal-scrollbar-measure {
  position: absolute;
  top: -9999px;
  width: 50px;
  height: 50px;
  overflow: scroll;
}

// Scale up the modal
@include media-breakpoint-up(sm) {
  // Automatically set modal's width for larger viewports
  .modal-dialog {
    max-width: $modal-md;
  }

  .modal-content {
    @include box-shadow($modal-content-box-shadow-sm-up);
  }

  .modal-sm { max-width: $modal-sm; }

  .modal-xl {
    max-width: $modal-xl;
  }
}

@include media-breakpoint-up(lg) {
  // Match spacing to container for larger viewports
  .modal-dialog {
    padding: 0 (map-get($container-padding, "lg") / 2);
  }

  .modal-lg {
    max-width: $modal-lg;
  }

  .modal-header {
    padding: $modal-header-padding-y $modal-header-padding-x-lg-up;

    // Pull close button into the modal-header padding area
    .close {
      @include ltr {
        margin-right: -$modal-header-padding-x-lg-up + $modal-header-padding-y;
      }
      @include rtl {
        margin-left: -$modal-header-padding-x-lg-up + $modal-header-padding-y;
      }
    }
  }

  .modal-body {
    padding: $modal-inner-padding-top $modal-inner-padding-x-lg-up $modal-inner-padding-bottom-lg-up;
  }

  .modal-footer {
    padding: $modal-footer-padding-y $modal-footer-padding-x-lg-up;
  }
}
