@import "../../styles/functions";
@import "../../variables";
@import "../../styles/mixins";
@import "mixins";
@import "text-field/text-field";
@import "form-check/form-check";
@import "interactive-star-rating/interactive-star-rating";

//
// Labels
//

label,
legend {
  font-size: $font-size-base; // only legal copy can use font smaller than base font size
  margin-bottom: $label-margin-bottom;
}

// For use with horizontal and inline forms, when you need the label (or legend)
// text to align with the form controls.

.col-form-label {
  padding-top: #{$font-size-base * $input-line-height - $font-size-base + $input-border-width};
  // padding-bottom: #{$font-size-base * $input-line-height - $font-size-base + $input-border-width};
  margin-bottom: 0; // Override the `<label>/<legend>` default
  font-size: $font-size-base; // Override the `<legend>` default
  line-height: $input-line-height;
}

.col-form-label-lg {
  padding-top: #{$font-size-lg * $input-line-height-lg - $font-size-lg + $input-border-width};
  // padding-bottom: #{$font-size-lg * $input-line-height - $font-size-lg + $input-border-width};
  margin-bottom: 0; // Override the `<label>/<legend>` default
  font-size: $font-size-lg;
  line-height: $input-line-height-lg;
}

.col-form-label-sm {
  padding-top: #{$font-size-sm * $input-line-height-sm - $font-size-sm + $input-border-width};
  // padding-bottom: #{$font-size-sm * $input-line-height - $font-size-sm + $input-border-width};
  margin-bottom: 0; // Override the `<label>/<legend>` default
  font-size: $font-size-sm;
  line-height: $input-line-height-sm;
}

// Form groups
//
// Designed to help with the organization and spacing of vertical forms. For
// horizontal forms, use the predefined grid classes.

.form-group {
  margin-bottom: $form-group-margin-bottom;
}

// Reduce space beneath form groups with inline controls, which have space beneath each item
.form-group-inline {
  margin-bottom: $form-group-inline-margin-bottom;
}

.form-text {
  display: block;
  margin-top: $form-text-margin-top;
  margin-bottom: 0;
}


// Form grid
//
// Special replacement for our grid system's `.row` for tighter form layouts.

.form-row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -5px;
  margin-left: -5px;

  > .col,
  > [class*="col-"] {
    padding-right: 5px;
    padding-left: 5px;
  }
}


// Form validation
//
// Provide feedback to users when form field values are valid or invalid. Works
// primarily for client-side validation via scoped `:invalid` and `:valid`
// pseudo-classes but also includes `.is-invalid` and `.is-valid` classes for
// server side validation.

// @include form-validation-state("valid", $form-feedback-valid-color, null);
@include form-validation-state("invalid", $form-feedback-invalid-color, map-get($glyphs, "warning"));

// Inline forms
//
// Make forms appear inline(-block) by adding the `.form-inline` class. Inline
// forms begin stacked on extra small (mobile) devices and then go inline when
// viewports reach <768px.
//
// Requires wrapping inputs and labels with `.form-group` for proper display of
// default HTML form controls and our custom form controls (e.g., input groups).

.form-inline {
  display: flex;
  flex-flow: row wrap;
  align-items: center; // Prevent shorter elements from growing to same height as others (e.g., small buttons growing to normal sized button height)

  // Because we use flex, the initial sizing of checkboxes is collapsed and
  // doesn't occupy the full-width (which is what we want for xs grid tier),
  // so we force that here.
  .form-check {
    width: 100%;
  }

  // Kick in the inline
  @include media-breakpoint-up(sm) {
    label {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 0;
    }

    // Inline-block all the things for "inline"
    .form-group {
      display: flex;
      flex: 0 0 auto;
      flex-flow: row wrap;
      align-items: center;
      margin-bottom: 0;
    }

    // Allow folks to *not* use `.form-group`
    .form-control {
      display: inline-block;
      width: auto; // Prevent labels from stacking above inputs in `.form-group`
      vertical-align: middle;
    }

    // Make static controls behave like regular ones
    .form-control-plaintext {
      display: inline-block;
    }

    .input-group,
    .custom-select {
      width: auto;
    }

    // Remove default margin on radios/checkboxes that were used for stacking, and
    // then undo the floating of radios and checkboxes to match.
    .form-check {
      display: flex;
      align-items: center;
      justify-content: center;
      width: auto;

      @include ltr {
        padding-left: 0;
      }
      @include rtl {
        padding-left: 0;
      }
    }

    .form-check-input {
      position: relative;
      margin-top: 0;

      @include ltr {
        margin-right: $form-check-input-spacer-x;
        margin-left: 0;
      }
      @include rtl {
        margin-right: 0;
        margin-left: $form-check-input-spacer-x;
      }
    }

    .custom-control {
      align-items: center;
      justify-content: center;
    }
    .custom-control-label {
      margin-bottom: 0;
    }
  }
}
