@import "../collapse/collapse";

.accordion {
  // remove default list margin and padding
  margin-bottom: 0;

  @include ltr {
    padding-left: 0;
  }

  @include rtl {
    padding-right: 0;
  }

  @at-root ul#{&} {
    list-style-type: $accordion-list-style-type;
  }

  .accordion-header {
    margin-bottom: $accordion-header-margin-bottom;
    border-top: $border-width solid $border-color;

    @include theme-inverse {
      border-color: $border-color-inverse;
    }
  }

  .btn-collapse {
    width: 100%;
    padding: $accordion-btn-padding-y $card-spacer-x;

    &::before {
      top: auto;
      margin-top: 0.1em;
    }

    @include ltr {
      text-align: left;
      padding-left: $accordion-glyph-padding-x;
    }

    @include rtl {
      text-align: right;
      padding-right: $accordion-glyph-padding-x;
    }
  }

  .accordion-body {
    padding-bottom: $accordion-content-padding-bottom;
    @include ltr {
      text-align: left;
      padding-left: $accordion-glyph-padding-x;
    }

    @include rtl {
      text-align: right;
      padding-right: $accordion-glyph-padding-x;
    }
  }
}
